
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
}




@import "react-alice-carousel/lib/scss/alice-carousel.scss";

/*----------------Colors--------------------*/
$base-color: linear-gradient(to left top, #6f8dc7, #2159cd);
$white: #fff;
$param-white: rgb(253, 253, 253);
$param-grey: rgb(196, 202, 235);
/*----------------/Colors--------------------*/

/*----------------Flex and Grid--------------------*/
@mixin flex-direction() {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

@mixin flex() {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@mixin noAlignd() {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

@mixin noJustD() {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@mixin noAlignF() {
    display: flex;
    justify-content: space-between;
}

@mixin grid() {
    display: grid;
    justify-items: center;
}

/*----------------/Flex and Grid--------------------*/

/*----------------Responsives--------------------*/
@mixin for-1600 {
    @media (max-width: 1600px) {
        @content;
    }
}

@mixin for-1400 {
    @media (max-width: 1400px) {
        @content;
    }
}

@mixin for-1300 {
    @media (max-width: 1300px) {
        @content;
    }
}

@mixin for-1100 {
    @media (max-width: 1100px) {
        @content;
    }
}

@mixin for-950 {
    @media (max-width: 950px) {
        @content;
    }
}

@mixin for-828 {
    @media (max-width: 828px) {
        @content;
    }
}

@mixin for-724 {
    @media (max-width: 724px) {
        @content;
    }
}

@mixin for-600 {
    @media (max-width: 600px) {
        @content;
    }
}

@mixin for-500 {
    @media (max-width: 500px) {
        @content;
    }
}

@mixin for-400 {
    @media (max-width: 400px) {
        @content;
    }
}

@mixin for-350 {
    @media (max-width: 350px) {
        @content;
    }
}

@mixin forh-1000 {
    @media (max-height: 1000px) {
        @content;
    }
}

/*----------------/Responsives--------------------*/

