/*------------Navbar Style--------------*/
.scroll {
    background-color: #333;
    opacity: 0.8;
    position: sticky;
    top: 0;
    opacity: 0.9;
    transition: all 0.4s linear;
    z-index: 999;
}

.nav {
    width: 100%;
    height: 100px;
    @include flex;
    padding: 0px 150px;
    transition: all 0.4s linear;

    @include for-1300 {
        padding: 0px 100px;
    }

    @include for-1100 {
        padding: 0px 80px;
        height: 80px;
    }

    @include for-950 {
        padding: 0px 50px;
    }

    @include for-500 {
        padding: 0px 25px;
    }

    &_logo {
        width: 100px;
        height: 100px;
        background-color: transparent;
        margin-left: -25px;

        @include for-1300 {
            margin-left: -0px;
        }

        @include for-1100 {
            width: 80px;
            height: 80px;
        }

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .listWidth {
        height:100%;
    }

    &_list {
        width: 250px;
        height: 100%;
        @include flex;
        list-style: none;
        @include for-1100 {
            width: 200px;
            &:last-child{
                width:250px;
            }
        }





        @include for-828 {
            position: absolute;
            top: -200px;
            left: 0;
            right: 0;
            width: 100%;
            height: 200px;
            padding: 20px;
            opacity: 0;
            @include flex-direction;
            transition: all 0.5s ease-out;

        }

        &_items {
            width: fit-content;

            &_get {
                border: 1px solid $white;
                padding: 10px;
                border-radius: 15px;

                & a {
                    color: $white;
                    font-size: 20px;

                    @include for-1100 {
                        font-size: 18px;
                    }
                }
            }

            & a {
                text-decoration: none;
                font-size: 18px;
                color: $white;
                transition: all 0.7 ease-in;

                @include for-1100 {
                    font-size: 16px;
                }

                &::after {
                    content: '';
                    width: 0%;
                    display: block;
                    margin: auto;
                    transition: all 0.5s linear;
                    border-radius: 12px;
                }

                &:hover::after {
                    width: 90%;
                    border: 1px solid $white;
                }
            }
        }
    }

    &_list.activeList {
        position: absolute;
        top: 80px;
        opacity: .9;
        height:300px;
        background-color: #333;
    }



    &_func {
        width: 50px;
        height: 100%;
        @include flex;

        @include for-828 {
            width: 100px;
        }







        &_mobile {
            width: 25px;
            height: 18px;
            opacity: 0;
            display: none;
            cursor: pointer;

            @include for-828 {
                opacity: 1;
                display: block;
                @include flex-direction;
            }

            &_line {
                width: 100%;
                border: 1px solid $white;
                border-radius: 8px;
            }

            &_line.activeBar:nth-child(1) {
                transform: rotate(-45deg);
                transform-origin: 25px;
            }

            &_line.activeBar:nth-child(2) {
                display: none;
            }

            &_line.activeBar:nth-child(3) {
                transform: rotate(45deg);
                transform-origin: 22px;
            }
        }
    }
}
/*------------/Navbar Style--------------*/

/*------------Select Buttons Style--------------*/
.sBtn {
    height:30px;
    width: 30px;
    position: relative;
    margin-left:120px;

    @include for-1300 {
        margin-left:80px;
    }
    @include for-950 {
        margin-left:50px;
    }
    @include for-828 {
        margin-left:0px;
    }
    &_btn {
        width:100%;
        height:30px;
        border: none;
        outline: none;
        background-color: transparent;
        color:#fff;
        font-size: 25px;
        cursor:pointer;

    }
    &_wrap {
        width:100%;
        height:90px;
        @include flex-direction;
        background-color: #fff;
        display: none;
        border-radius: 8px;
        &_btn {
            color:#333;
            width:100%;
            height:30px;
            border: none;
            outline: none;
            background-color: transparent;
            font-size: 16px;
            cursor:pointer;
            &:hover {
                background-color: #2159cd;
                color:#fff;
            }
        }


    }
    .sBtnActive {
        display:block;
    }
}

/*------------/Select Buttons Style--------------*/

/*------------Header Style--------------*/
.Home {
