    @include for-1100 {
        padding: 20px 40px 20px 40px;
    }

    @include for-950 {
        padding: 20px 30px 20px 30px;
    }

    @include for-828 {
        padding: 20px;
    }

    @include for-400 {
        padding: 10px;
    }

    &_top {
        padding: 0px 100px;
        width: 100%;
        height: 400px;
        @include flex;
        gap: 20px;


        @include for-1300 {
            padding: 0px 80px;
            height: 350px;
        }

        @include for-1100 {
            padding: 0px 50px;
            height: 320px;
        }

        @include for-950 {
            padding: 0px 30px;
        }

        @include for-828 {
            padding: 0px 0px;
        }

        @include for-724 {
            @include flex-direction;
            height: 250px;
            margin-top: 50px;
            gap: 0px;
        }

        @include for-500 {
            @include flex-direction;
            height: 220px;
        }



        &_title {
            width: 600px;
            height: 40%;
            font-size: 54px;
            font-weight: bold;
            color: $white;



            @include for-1300 {
                font-size: 44px;
                width: 500px;
                height: 40%;
            }

            @include for-1100 {
                font-size: 40px;
                width: 470px;
                height: 40%;
            }

            @include for-950 {
                font-size: 36px;
                width: 450px;
                height: 40%;
            }

            @include for-724 {
                font-size: 30px;
                width: 400px;
                height: 30%;
                text-align: center;
            }

            @include for-500 {
                font-size: 26px;
                width: 350px;
                height: 30%;
            }

            @include for-400 {
                font-size: 24px;
                width: 300px;
                height: 30%;

            }

        }


        &_desc {
            margin-top: 30px;
            width: 350px;
            height: 160px;
            @include noAlignd;
            list-style: none;


            @include for-1300 {
                width: 320px;
                height: 140px;
                margin-top: 10px;
            }

            @include for-1100 {
                width: 300px;
                height: 130px;
                margin-top: 10px;
            }

            @include for-500 {
                width: 270px;
                height: 130px;
                margin-top: 10px;
            }


            &_list {
                width: 100%;
                height: 130px;
                @include noAlignd;
                list-style: none;
                padding-bottom: 20px;

                @include for-1300 {
                    padding-bottom: 15px;
                    height: 110px;
                }

                @include for-1100 {
                    width: 300px;
                    height: 130px;
                    margin-top: 10px;
                }

                @include for-400 {
                    width: 280px;
                }



                &_item {
                    font-size: 20px;
                    color: $white;

                    @include for-1300 {
                        font-size: 18px;
                    }

                    @include for-1100 {
                        font-size: 16px;
                    }

                }

            }

            &_Getbtn {
                width: 210px;
                padding: 10px;
                height: 40px;
                border: none;
                border-radius: 30px;
                background-image: $base-color;

                @include for-1300 {
                    height: 35px;
                }

                @include for-1100 {
                    height: 30px;
                    width: 200px;
                    @include flex-direction;
                }



