    &_center {
        width: 100%;
        height: 100%;
        padding: 10px 20px;
        margin-top: 0px;
        @include flex;
        z-index: 99;

        @include for-1300 {
            padding: 10px 0px;
        }

        @include for-724 {
            margin-top: 50px;
            @include flex-direction;
            height: 900px;
            padding: 10px 50px;
        }

        @include for-600 {
            padding: 10px 20px;
        }

        @include for-500 {
            padding: 10px 0px;
            height: 850px;
        }




        &_left {
            width: 70%;
            height: 510px;
            background-image: $base-color;
            padding: 20px;
            border-radius: 20px;

            @include for-1300 {
                height: 440px;
            }

            @include for-1100 {
                height: 400px;
            }

            @include for-724 {
                width: 100%;
                border-radius: 8px;
            }


            @include for-400 {
                height: 380px;
                padding: 25px 0px;
            }



            & .loading {
                color: $white;
                text-align: center;
            }

            &_wrap {
                width: 100%;
                height: 100%;

                &_nav {
                    width: 100%;
                    color: $white;
                    @include grid;
                    grid-template-columns: repeat(4, 1fr);
                    justify-items: center;
                    align-content: center;


                    & span {
                        font-size: smaller;


                        @include for-500 {
                            font-size: 12px;
                            width: 70%;
                            margin-left: 20px;
                        }

                        @include for-400 {
                            font-size: 12px;
                            width: 60%;
                        }
                    }
                }






                &_curr {
                    @include grid;
                    grid-template-columns: repeat(4, 1fr);
                    padding: 25px 0px;
                    justify-items: center;

                    @include for-1300 {
                        padding: 20px 0px;
                    }

                    @include for-1100 {
                        padding: 16px 0px;
                    }


                    &_market {
                        width: 70px;
                        position: relative;

                        @include for-950 {
                            margin-left: 20px;
                        }

                        @include for-400 {
                            margin-left: 25px;
                            width: 50px;
                        }

                        @include for-350 {
                            width: 40px;
                            margin-left: 15px;
                        }

                        & span {
                            text-transform: uppercase;

                            @include for-950 {
                                font-size: 12px;
                            }

                            @include for-400 {
                                font-size: 8px;
                            }
                        }

                        &_img {
                            position: absolute;
                            left: -70px;
                            top: 0px;

                            @include for-1300 {
                                left: -50px;
                                width: 40px;
                                height: 40px;
                            }

                            @include for-1100 {
                                left: -40px;
                                width: 35px;
                                height: 35px;
                            }

                            @include for-500 {
                                left: -30px;
                                width: 25px;
                                height: 25px;
                            }

                            @include for-400 {
                                left: -25px;
                                width: 20px;
                                height: 20px;
                            }

                            @include for-350 {
                                top: 2px;
                                left: -20px;
                                width: 18px;
                                height: 18px;
                            }

                            & img {
                                height: 100%;
                                height: 100%;
                                object-fit: cover;

                            }
                        }

                        & h5 {
                            color: $white;
                            font-size: 18px;
                            font-weight: 300;

                            @include for-950 {
                                font-size: 16px;
                            }

                            @include for-500 {
                                font-size: 14px;
                            }



                        }
                    }


                    &_price {
                        width: 100px;
                        color: aqua;
                        text-align: center;

                        @include for-500 {
                            width: 50px;
                        }

                        @include for-400 {
                            margin-left: 10px;
                        }

                        @include for-350 {
                            width: 40px;
                        }



                        & h5 {
                            font-size: 12px;
                            margin-top: 3px;
                        }
                    }


                    &_change {
                        width: 100%;
                        color: antiquewhite;
                        text-align: center;

                        @include for-500 {
                            width: 50px;
                        }

                        @include for-400 {
                            margin-left: 20px;
                        }

                        @include for-350 {
                            width: 40px;
                            margin-left: 10px;
                        }

                        & h5 {
                            font-size: 12px;
                            margin-top: 3px;
                        }


                    }

                    &_chart {
                        width: 60px;

                        @include for-400 {
                            width: 50px;
                        }
                    }
                }
            }
        }



        &_right {
            width: 28%;
            height: 510px;
            padding: 60px 40px;
            background-color: rgb(158, 186, 36);
            @include noAlignd;
            gap: 30px;
            font-size: 30px;
            border-radius: 20px;

            @include for-1300 {
                height: 440px;
                padding: 50px 20px;
            }

            @include for-1100 {
                height: 400px;
                padding: 40px 20px;
            }

            @include for-950 {
                padding: 30px 15px;
            }

            @include for-724 {
                width: 100%;
                border-radius: 8px;
            }




            & img {
                width: 200px;
                height: 200px;
                object-fit: contain;
                margin: auto;

                @include for-1100 {
                    width: 150px;
                    height: 150px;
                }

                @include for-724 {
                    width: 200px;
                    height: 200px;
                }

                @include for-400 {
                    width: 150px;
                    height: 150px;
                }


            }


            & h5 {
                text-align: center;

                @include for-1100 {
                    font-size: 18px;
                }

                @include for-724 {
                    font-size: 20px;
                }

            }




            & p {
                font-size: 14px;
                text-align: center;
                color: #333;

                @include for-950 {
                    font-size: 12px;
                }

                @include for-724 {
                    font-size: 14px;
                    padding: 20px 30px;
                }
                @include for-500 {
                    @include forh-1000 {
                        margin-top:-30px;
                    }
                }
            }
        }
    }
}



