/*------------Schedule Style--------------*/
.schedAnimation {
    display: flex;
    position: relative;
    width:100vw;
    height:100vh;
    z-index: 999;
    @include for-950 {
        height: 80vh;
    }


    @include for-828 {
        height: 60vh;
    }
    @include for-600 {
        height: 350px;
    }
}
.schedule {
    width:100%;
    height: 800px;
    margin-top:100px;
    padding:50px;
    display: flex;
    position: relative;

    @include for-950 {
        padding:30px;
        margin-top: 70px;
        height:600px;
    }

    @include for-828 {
        height:500px;
    }
    @include for-724 {
        padding:70px;
        margin-top:10px;
        height:350px;
    }
    @include for-600 {
        padding:40px;
        margin-top:0px;
        height: 300px;
    }

    @include for-500 {
        padding:20px;
    }

    @include for-400 {
        padding:10px;
    }


    &_wrap {
        width:100%;
        height:100%;
        border-radius: 20px;
        overflow: hidden;
        z-index: 98;

        @include for-724 {
            border-radius: 8px;
        }
        &_image {
            width: 100%;
            height: 100%;
            object-fit: fill;
            cursor:pointer;
            transition: all 0.5s linear;
        }
        .imageActive  {
            width: 100%;
            height:100%;
            object-fit: fill;
            cursor:pointer;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            transition: all 0.5s linear;
            @include for-724 {
                object-fit: cover;
            }
        }
    }
}





